import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { TYPE } from "theme";
import axios from "axios";
import uuid from "uuid";
import { FileUploader } from "react-drag-drop-files";

import { utils } from "ethers";
import NumericalInput from "components/NumericalInput";
import { AutoColumn } from "components/Column";
import TextareaAutosize from "react-textarea-autosize";
import { ButtonPrimary } from "components/Button";
import { useERCFactory } from "hooks/useContract";
import { calculateGasMargin } from "../../utils/calculateGasMargin";
import { useTransactionAdder } from "state/transactions/hooks";
import { useIsTransactionPending } from "state/transactions/hooks";
import { Redirect } from "react-router-dom";
import { useAllTransactions } from "state/transactions/hooks";
import ERCFACTORY_ABI from "abis/ercfactory.json";
import { textAlign } from "styled-system";

const fileTypes = ["JPEG", "JPG", "PNG"];

const JWT =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..v0CuSmfV1cQLs9EkT1BrVm2weKGlTNNK3ZvKR4uYceM";

const StyledDiv = styled.div`
  padding: 18px;
  background-color: ${({ theme }) => theme.bg0};
  border-radius: 24px;
`;
const StyledInput = styled.input`
  text-align: center;
  background-color: ${({ theme }) => theme.bg1};
  margin-right: 12px;
  font-size: 18px;
  margin: 0 auto;
  text-align: left;
  width: 100%;
  min-height: 40px;
  color: ${({ theme }) => theme.text2};
  border: 1px solid #2c2f36;
  font-weight: 500;
`;

const StyledNumInput = styled(NumericalInput)`
  text-align: center;
  margin-right: 12px;
  background-color: ${({ theme }) => theme.bg1} !important;
  font-size: 18px;
  margin: 0 auto;
  text-align: left;
  position: inherit !important;
  width: 100%;
  min-height: 40px;
  border: 1px solid #2c2f36;
  font-weight: 500;
`;

const InputTitle = styled(TYPE.mediumHeader)`
  color: ${({ theme }) => theme.text2};
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 10px;
`;

const TextArea = styled(TextareaAutosize)`
  color: ${({ theme }) => theme.text2};
  font-size: 18px;
  border: 1px solid #2c2f36;
  font-weight: 500;
  margin-bottom: 10px;
  min-height: 50px;
  background-color: ${({ theme }) => theme.bg1};
  border: 1px solid #2c2f36;
`;

const MB = { marginBottom: "12px" };
const MB2 = { marginBottom: "12px", textAlign: "center" };

const Start: React.FC = () => {
  const [text, setText] = useState("");
  const [description, setDescription] = useState("");
  const [telegram, setTelegram] = useState("");
  const [s, setS] = useState("0");
  const [x, setX] = useState("");
  const [website, setWebsite] = useState("");
  const [name, setName] = useState("");
  const transactionAdder = useTransactionAdder();
  const [image, setImage] = useState("");
  const [txHash, setTXHash] = useState(null);
  const pendingTX = useIsTransactionPending(txHash ?? undefined);
  const allTX = useAllTransactions();

  const contract = useERCFactory();

  const [file, setFile] = useState<any>(null);
  const handleChange = (fileURL: any) => {
    const formData = new FormData();

    formData.append("file", fileURL);

    const pinataMetadata = JSON.stringify({
      name: fileURL.name,
    });
    formData.append("pinataMetadata", pinataMetadata);

    const pinataOptions = JSON.stringify({
      cidVersion: 1,
    });
    formData.append("pinataOptions", pinataOptions);

    fetch("https://api.pinata.cloud/pinning/pinFileToIPFS", {
      method: "POST",
      headers: {
        Authorization: `Bearer ${JWT}`,
      },
      body: formData,
    })
      .then((r) => r.json())
      .then((response) => {
        console.log(response.IpfsHash);

        setFile(file);
        // setImage(
        //   `https://bronze-fascinating-guanaco-829.mypinata.cloud/ipfs/${response.IpfsHash}?pinataGatewayToken=kOVtU4LNJHYr1v87kBNeUeZWg6B5eFoM_y1kE1WgjF97NhrDi2fXTecq6GiE1m97`
        // );
        setImage(response.IpfsHash);
      })
      .catch(console.log);
  };

  useEffect(() => {}, [pendingTX]);

  if (txHash != null && pendingTX === false) {
    const receipt = allTX?.[txHash];

    // Create an Interface for decoding logs
    const contractInterface = new utils.Interface(ERCFACTORY_ABI);

    let tokenAddress = "";

    // Loop through logs in the receipt
    if (receipt && receipt.receipt && receipt.receipt.logs) {
      for (const log of receipt.receipt.logs) {
        try {
          // Decode the log using the contract's interface
          const parsedLog = contractInterface.parseLog(log);

          if (parsedLog.name == "TokenCreated") {
            tokenAddress = parsedLog.args[0];
          }

          // Check if the log matches the desired event (optional)
        } catch (error) {
          // Log decoding error (happens if log isn't from your contract)
          console.error("Error parsing log:", error);
        }
      }
      const n = uuid.v4();
      const numericPart = parseInt(n.replace(/-/g, "").slice(0, 15), 16);
      axios
        .post(
          "/update-json",
          {
            pathToUpdate: "tokens",
            newObject: {
              name: name,
              symbol: text,
              decimals: 18,
              address: tokenAddress,
              chainId: 181,
              logoURI: image,
            },
          },
          { headers: { "Content-Type": "application/json" } }
        )
        .then((res) => {
          axios.post(
            "/update-json",
            {
              pathToUpdate: "version.minor",
              newObject: numericPart,
            },
            { headers: { "Content-Type": "application/json" } }
          );
        });
      return <Redirect to={`/add/WATER/${tokenAddress}`} />;
    }
  }

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();

    if (!contract || !name || !text || s === "0") {
      return;
    }
    const wtx =
      website.slice(0, 512) +
      "|" +
      telegram.slice(0, 100) +
      "|" +
      x.slice(0, 100);

    contract.estimateGas
      .createToken(
        name,
        text.slice(0, 5).toUpperCase(),
        s,
        wtx,
        description,
        image
      )
      .then((estimatedGasLimit) => {
        console.log("estimatedGasLimit", estimatedGasLimit);

        contract
          .createToken(
            name,
            text.slice(0, 5).toUpperCase(),
            s,
            wtx,
            description.slice(0, 512).toUpperCase(),
            image,
            { gasLimit: calculateGasMargin(estimatedGasLimit) }
          )
          .then((txid: any) => {
            console.log("txid", txid);

            transactionAdder(txid, { summary: "Token created: " + name });

            setTXHash(txid.hash);
          })
          .catch(console.error);
      })
      .catch(console.error);
  };

  return (
    <StyledDiv>
      <form onSubmit={handleSubmit}>
        <AutoColumn gap="6px">
          <h2 style={MB2}>Start a new coin</h2>
          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              Name
            </InputTitle>
            <StyledInput
              value={name}
              inputMode="text"
              disabled={false}
              onChange={(e) => {
                setName(e.target.value);
              }}
              placeholder={" "}
            />
          </div>
          {/* <InputTitle fontSize={12} textAlign="center">
            new
          </InputTitle> */}

          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              Ticker
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={text}
              onChange={(e) => {
                setText(e.target.value.toUpperCase());
              }}
              placeholder={" "}
            />
          </div>
          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              Total supply
            </InputTitle>
            <StyledNumInput
              inputMode="text"
              value={s}
              onUserInput={(text) => {
                setS(Number(text));
              }}
              placeholder={" "}
            />
          </div>
          <div style={MB}>
            <label>
              <InputTitle fontSize={14} textAlign="center">
                Description
              </InputTitle>{" "}
              <TextArea
                cols={34}
                value={description}
                rows={5}
                onChange={(e) => setDescription(e.target.value)}
              />
            </label>
          </div>
          <div className="App">
            <div style={MB2}>Image</div>
            <FileUploader
              required={false}
              handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
            <p>{file ? `File name: ${file.name}` : "no files uploaded yet"}</p>
          </div>
          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              Telegram
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={telegram}
              onChange={(e) => {
                console.log(e, e.target);
                setTelegram(e.target.value);
              }}
              placeholder={" "}
            />
          </div>
          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              Website
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={website}
              onChange={(e) => setWebsite(e.target.value)}
              placeholder={" "}
            />
          </div>
          <div style={MB}>
            <InputTitle fontSize={14} textAlign="center">
              X.com link
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={x}
              onChange={(e) => setX(e.target.value)}
              placeholder={" "}
            />
          </div>
          <ButtonPrimary
            disabled={pendingTX || s === "0" || name === "" || text === ""}
          >
            Submit
          </ButtonPrimary>
        </AutoColumn>
      </form>
    </StyledDiv>
  );
};

export default Start;
