import React from "react";
import styled from "styled-components";
import { TYPE } from "theme";

import { AutoColumn } from "components/Column";

import { useAllTokens } from "hooks/Tokens";
import { CardSection } from "../../components/earn/styled";
import { RowBetween } from "../../components/Row";
import { DarkCard } from "components/Card";
import { PaddedColumn } from "components/SearchModal/styleds";
import CurrencyLogo from "components/CurrencyLogo";
import CurrencyPrice from "components/CurrencyPrice";
import CurrencySupply from "components/CurrencySupply";

const TopSection = styled(AutoColumn)`
  width: 100%;
  display: flex;
  justify-content: center;
`;
const Card = styled(DarkCard)`
  width: 100%;
  max-width: 300px;
`;
const Row = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 16px; // Optional padding
  justify-content: space-between;
`;

const CoinRow = styled(Row)`
  display: flex;
  justifycontent: center;
  padding: 18px;
`;

const P = { cursor: "pointer" };
const J = { display: "flex", justifyContent: "center" };
const O = { overflow: "hidden" };
const F = { fontSize: "18px", fontWeight: 600 };
const TO = {
  textOverflow: "ellipsis",
};

const Coins: React.FC = ({ history }: any) => {
  const tokens = useAllTokens();
  const addresses = Object.keys(tokens).slice().reverse();

  if (addresses == null || addresses.length == 0) {
    return null;
  }

  addresses.push(addresses?.shift());
  addresses.push(addresses?.shift());

  return (
    <CoinRow>
      {addresses.map((t: string) => {
        const token = tokens[t];

        return (
          <PaddedColumn key={t} gap="sm">
            <TopSection gap="md">
              <Card
                style={P}
                onClick={(e) => {
                  history.push(`/swap/${token.address}`);
                  return e;
                }}
              >
                <CardSection>
                  <AutoColumn gap="md">
                    <RowBetween style={J}>
                      <CurrencyLogo
                        currency={token}
                        size={"220px"}
                      ></CurrencyLogo>
                    </RowBetween>
                    <RowBetween style={F}>
                      {token.symbol?.toUpperCase()}
                    </RowBetween>
                    <RowBetween style={O}>
                      <TYPE.gray fontSize={12} style={TO}>
                        {token.address}
                      </TYPE.gray>
                    </RowBetween>
                    <RowBetween style={O}>
                      Price: <CurrencyPrice currency={token} />
                    </RowBetween>
                    <RowBetween style={O}>
                      Supply: <CurrencySupply currency={token} />
                    </RowBetween>
                  </AutoColumn>
                </CardSection>
              </Card>
            </TopSection>
          </PaddedColumn>
        );
      })}
    </CoinRow>
  );
};

export default Coins;
